@import url(https://fonts.googleapis.com/css2?family=Comfortaa:wght@300;700&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Spartan:wght@100;400;600&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Righteous&display=swap);
.Tracklist-container {
  background: rgba(0, 0, 0, 0.5);
}

.component-container {
  border-radius: 9px;
  box-shadow: inset 1px 1px 3px rgba(0, 0, 0, 0.5),
    inset -2px -2px 5px rgba(0, 0, 0, 0.5);
  width: 100vw;
}

.flex-container {
  display: flex;
  flex-wrap: wrap;
  text-align: center;
  justify-content: center;
  align-content: center;
}

.flex-row {
  flex-direction: row;
}

.flex-col {
  flex-direction: column;
  align-items: center;
}

.grid-container {
  display: grid;
  grid-template-columns: repeat(1, [col] auto);
  grid-template-rows: repeat(3, [row] auto);
  margin: 1% auto;
  padding: 0 1%;
}

.Title {
  flex: 1 1;
  font-weight: bolder;
  font-size: 4rem;
  text-align: center;
  background-color: #fff;
  border-radius: 9px;
  box-shadow: inset 1px 1px 3px rgba(0, 0, 0, 0.5),
    inset -2px -2px 5px rgba(0, 0, 0, 0.5);
}

.track-name {
  font-weight: bolder;
  font-size: 3.2rem;
}

.dropdownMenu {
  border-radius: 9px;
  box-shadow: 1px 1px 3px rgba(0, 0, 0, 0.5), -2px -2px 5px rgba(0, 0, 0, 0.5);
}

.select {
  background: none;
  border-radius: 9px;
  box-shadow: inset 1px 1px 3px rgba(0, 0, 0, 0.5),
    inset -2px -2px 5px rgba(0, 0, 0, 0.5);
  width: 100%;
  border-radius: 5px;
  border: none;
  border: 1px solid rgba(0, 0, 0, 0.5);
  outline: none;
  text-align: center;
}

.card-container {
  background-color: #fff;
  border-radius: 9px;
  box-shadow: inset 1px 1px 3px rgba(0, 0, 0, 0.5),
    inset -2px -2px 5px rgba(0, 0, 0, 0.5);
}

.album-img {
  margin-top: 10%;
  margin-bottom: 10%;
}
.track-artist {
  margin-bottom: 10%;
}
.track-album {
  margin-bottom: 10%;
}
.square-wrapper {
  display: flex;
  position: relative;
  width: 70%;
}
.square-wrapper:after {
  content: "";
  display: block;
  padding-bottom: 100%;
}
.square {
  position: absolute;
  width: 100%;
  height: 100%;
}
.tracklist-wrapper {
  display: grid;
  grid-gap: 1rem;
  grid-template-columns: repeat(1, [col] auto);
}

.track-detail {
  width: 45rem;
  height: 12rem;
  padding: 1rem;
  overflow: auto;
}
.track-name {
  font-size: 28px;
}
.track-artist {
  font-size: 22px;
}
.track-album {
  font-size: 22px;
}

/* If [device width] is greater than or equal to 600px, then do {…} */
@media only screen and (min-width: 1200px) {
  .square-wrapper {
    display: flex;
    position: relative;
    width: 30%;
  }
}

/* If [device width] is less than or equal to 600px, then do {…} */
@media only screen and (max-width: 600px) {
  #root {
    width: 100%;
    height: 100%;
  }

  .component-container {
    width: 100vw;
  }

  .Tracklist-container {
    width: 100%;
  }

  .grid-container {
    display: flex;
  }

  .details-container {
    margin: 0 auto;
    padding: 0;
  }

  .details {
    display: flex;
    justify-content: center;
    align-items: center;
    padding: 0;
  }

  .sub-title {
    display: none;
  }
  .sub-detail {
    font-size: 2.5rem;
  }

  .grid-container {
    display: flex;
    flex-direction: column;
    align-items: center;
    height: 50%;
    width: 100vw;
  }
}

/* NAV */
.Nav.container {
  height: 30%;
  flex: 1 1;
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.Nav {
  flex: 1 1;
  display: flex;
  width: 100vw;
  border-radius: 1.5rem;
  color: white;
  background: #1db954;
  box-shadow: inset 2px 2px 5px #116f32, inset -2px -2px 5px #a5e3bb;
}

.Nav:hover {
  box-shadow: inset 2px 2px 5px #116f32, inset -2px -2px 5px #a5e3bb;
}

.Nav a {
  color: white;
  display: inline-block;
  padding: 2.5rem;
  margin: 0.5rem;
}

.Nav a:hover {
  background: rgba(255, 255, 255, 0.6);
  border-radius: 35px;
}
.moodmix {
  font-family: "Righteous", cursive;
  font-size: calc(1rem + 12vmin);
  text-shadow: 0 9px 7px rgba(0, 0, 0, 0.8);
}

.logo.title.nav-container {
  display: flex;
  flex-direction: row;
  align-items: flex-start;
}

.nav-container {
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  margin: 1rem 2rem;
}

/* FORM */
.form-container {
  display: flex;
  justify-content: flex-end;
}

/* SEARCHBAR */
.search-wrapper.green-search-wrapper {
  flex: 1 1;
  background-color: #1db954;
}

.searchbar.green-searchbar {
  box-shadow: inset 7px 7px 7px #116f32, inset -8px -8px 9px #77d598;
  color: #ffffff;
}
.searchbar:hover.green-searchbar:hover {
  box-shadow: inset 9px 9px 3px #116f32, inset -8px -8px 8px #77d598;
}
.searchbar:focus.green-searchbar:focus {
  box-shadow: inset -8px -8px 6px #77d598, inset 8px 8px 3px #116f32;
}

/* SEARCH BTN */
.green-btn-container.btn-container {
  border: 0.01rem solid #77d598;
  border: 0.01rem solid rgba(0, 0, 0, 0.1);
  background-color: #1db954;
}
.green-btn-container.btn-container:hover {
  box-shadow: 0 0 5px #0f5d2a, 0 0 5px #bbeacc;
}
.green-btn.btn {
  border: 1px solid #77d598;
  border: 0.01rem solid rgba(0, 0, 0, 0.1);

  background-color: #1db954;
  box-shadow: 1rem 1rem 2rem #116f32, inset -2rem -2rem 1rem #61ce87;
}
.green-btn:hover.btn:hover {
  box-shadow: 0 0 5px #0f5d2a, 0 0 3px #bbeacc;
}
.green-btn:active.btn:active {
  box-shadow: inset 2rem 2rem 1rem #116f32, inset -2rem -2rem 1rem #77d598;
}

.DropdownMenu {
  width: 100%;
  height: 100%;
  margin: 1rem;
  border: 1px solid rgba(0, 0, 0, 0.5);
  border-radius: 5px;
  box-shadow: 1px 1px 3px rgba(0, 0, 0, 0.5), -2px -2px 5px rgba(0, 0, 0, 0.5);
}

.Select {
  width: 100%;
  border-radius: 5px;
  border: none;
  border: 1px solid rgba(0, 0, 0, 0.5);
  outline: none;
  background-color: #1db954;
  text-align: center;
}
.option {
  width: 90%;
  background-color: red;
}

/* FONTS */

/*  RESETS */
*,
*::after,
*::before {
  margin: 0;
  padding: 0;
  box-sizing: inherit;
}

* {
  margin: 0;
  padding: 0;
  font-size: 36px;
  font-family: "Spartan", sans-serif;
}

a {
  color: inherit;
  text-decoration: none;
}

ul {
  list-style-type: none;
}

html {
  font-size: 10px;
}

input:focus,
textarea:focus,
select:focus {
  outline: none;
  cursor: pointer;
}

